export default "module MyNum(\n  input [3:0] a,\n  output [3:0] res);\n\n  assign res = a;\nendmodule\n\nmodule Main(\n  input [3:0] x,\n  output [3:0] y); \n\n  wire q,w,e,r;\n  MyNum mynum(.a(x), .res({q,w,e,r}));\n\n  assign y = {q,w,e,r};\n\n  // Check to see if Qs = Qus for all combinations of a,b,c\n  test begin\n    #0  { x=4'b1010 };\n    #2  { x=4'b1110 };\n    #4  { x=4'b0001 };\n    #6  { x=4'b1000 };\n    #8;\n  end\nendmodule";