export default "/* 1 to 2 DeMultiplexer\nSel selects which output to send the input to\nsel a | Y Z\n 0  0 | 0 0 \n 0  1 | 1 0 \n 1  0 | 0 0 \n 1  1 | 0 1 \n */\n\nmodule DeMux (\n\tinput sel,\n\tinput a,\n\toutput Y, Z);\n\n\tassign Y = ~sel & a;\n  assign Z = sel & a;\nendmodule\n\nmodule Main (\n  input sel, clock,\n  output Y, Z);\n\n  // \"clock\" is an automatic wire in main\n  DeMux demux(.a(clock), .sel(sel), .Y(Y), .Z(Z));\n\n  test begin\n    #001 {sel=0};  \n    #010 {sel=1}; \n    #020;\n  end\nendmodule";