export default "// Master-slave d-flipflop\n\nmodule DFlipFlop (\n  input clk, reset, d,\n  output reg Q);\n\n  initial Q=1;\n\n  always @(posedge clk)\n    begin\n      if (reset) Q=0;\n      else Q=d;\n    end\nendmodule\n\n\nmodule Main(\n  input c, reset, d,\n  output Qm\n); \n\n  DFlipFlop dff(\n\t\t.clk(c),\n\t\t.reset(reset),\n    .d(d),\n\t\t.Q(Qm)\n  );\n\n  test begin\n    #0  { d=0, c=0, reset=0 };\n    #1  { d=0, c=0, reset=0 };\n    #2  { d=0, c=1, reset=0 };\n    #3  { d=1, c=1, reset=0 };\n    #4  { d=0, c=1, reset=0 };\n    #5  { d=0, c=0, reset=0 };\n    #6  { d=1, c=1, reset=0 };\n    #7  { d=0, c=1, reset=0 };\n    #8  { d=1, c=1, reset=0 };\n    #9  { d=1, c=0, reset=0 };\n    #10 { d=1, c=1, reset=1 };\n    #12;\n  end\nendmodule";