export default "// sign extend\n// extend a 16 bit number to 32 bits\n// copy the sign bit (most significant bit) into the upper 16 bits\n\nmodule SignExtend(\n  input [15:0] a,\n  output reg [31:0] y\n);\n  always @(*)\n    y = { { 16 { a[15] } }, a};\nendmodule\n\nmodule Main (\n  input [15:0] a,\n  output [31:0] y\n);\n\n  SignExtend se(a,y);\n\n  test begin\n    #0  { a=16'b1010101010101010 }; \n    #1  { a=16'b0101010101010101 }; \n    #2  { a=16'b1000000000000000 }; \n    #3;\n  end\n\nendmodule";