export default "// 4 bit adder behavioural model (experimental)\n\nmodule Adder #(parameter N=8) (\n  input [N-1:0] a, b,\n  input cin,\n  output reg [N-1:0] sum,\n  output reg cout\n);\n  always @(*)\n  begin\n    {cout, sum} = a + b + cin;\n  end\nendmodule\n\nmodule Main(\n  input [7:0] x, y,\n  input cin,\n  output [7:0] SUM, COUT\n); \n\n  Adder #(4) myAdder  (\n\t\t.a(x),\n\t\t.b(y),\n    .cin(cin),\n\t\t.sum(SUM),\n\t\t.cout(COUT)\n  );\n\n  test begin\n    #0  { cin=0, x=0, y=0 };\n    #1  { cin=0, x=1, y=8 };\n    #2  { cin=0, x=3, y=7 };\n    #3  { cin=0, x=5, y=6 };\n    #4  { cin=0, x=7, y=5 };\n    #5  { cin=0, x=8, y=8 };\n    #6;\n  end\nendmodule";