export default "// ALU\n// Based on Digital Design and Computer Architecture fig 5.15\n// 000 A AND B\n// 001 A OR B\n// 010 A + B\n// 011 not used\n// 100 A and ~B\n// 101 A or ~B\n// 110 A - B\n// 111 SLT\n\nmodule Add #(parameter N = 8) (\n  input [N-1:0] a, b,\n  input cin,\n  output reg [N-1:0] y,\n  output reg cout\n);\n  always @(*)\n  begin\n    {cout, y} = a + b + cin;\n  end\nendmodule\n\nmodule Mux2 #(parameter N=8) (\n  input [N-1:0] a, b,\n  input sel,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    case(sel)\n      // abcdefg\n      0: y = a;\n      1: y = b;\n      default: y = a;\n    endcase\n  end\nendmodule\n\nmodule Mux4 #(parameter N=8) (\n  input [N-1:0] a, b, c, d,\n  input [1:0] sel,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    case(sel)\n      // abcdefg\n      0: y = a;\n      1: y = b;\n      2: y = c;\n      3: y = d;\n      default: y = a;\n    endcase\n  end\nendmodule\n\nmodule Alu #(parameter N=8) (\n  input [2:0] F,\n  input [N-1:0] a, b,\n  output [N-1:0] y,\n  output cout\n); \n\n  wire [N-1:0] notb;\n  assign notb = ~b;\n\n  wire [N-1:0] bb;\n  Mux2 #(N) bbmux (.a(b), .b(notb), .sel(F[2]), .y(bb));\n  \n  wire [N-1:0] aandbb, aorbb;\n  assign aandbb = a & bb;\n  assign aorbb = a | bb;\n\n  wire [N-1:0] S;\n  Add #(N) adder (.a(a), .b(bb), .cin(F[2]), .y(S), .cout(cout));\n\n  Mux4 ymux(.a(aandbb), .b(aorbb), .c(S), .d(S[N-1]), .sel(F[1:0]), .y(y));\nendmodule\n\n\nmodule Main (\n  input [2:0] F,\n  input [7:0] a, b,\n  output [7:0] y,\n  output cout\n);\n\n  Alu #(8) alu (.F(F), .a(a), .b(b), .y(y), .cout(cout));\n\n  test begin\n    #0  { F=0, a=2, b=3 }; // AND\n    #1  { F=1, a=2, b=3 }; // OR\n    #2  { F=2, a=2, b=3 }; // +\n    #3  { F=4, a=2, b=3 }; // A AND ~B\n    #4  { F=5, a=2, b=3 }; // A OR ~B\n    #5  { F=6, a=2, b=3 }; // -\n    #6  { F=7, a=2, b=3 }; // SLT = A - B which is negative (N-1 bit is 1) if A < B\n    #7;\n  end\n\nendmodule";