export default "// One-hot up counter from Chapter 7.5.5 of Introduction to Logic Circuits and Logic Design LaMeres\n\n// TODO: Not working. Need to fix flip flop ? simulator issue\n\nmodule DFlipFlop (\n  input d, c, clear, preset,\n  output Q, Qn);\n\n  wire notd, notc;\n  not(notd, d);\n  not(notc, c);\n\n  wire u1, u2, u3, u4, u5, u6, u7, u8;\n\n  // master\n  nand(u3, d, c, clear);\n  nand(u4, notd, c, preset);\n  nand(u5, u3, u6, preset);\n  nand(u6, u4, u5, clear);\n\n  // slave\n  nand(u7, u5, notc);\n  nand(u8, u6, notc);\n  nand(Q,  u7, Qn, preset);\n  nand(Qn, u8, Q, clear);\nendmodule\n\nmodule NextState (\n  input Q0cur, Q1cur, Q2cur,\n  output Q0nxt, Q1nxt, Q2nxt);\n\n  buffer(Q0nxt, Q2cur);\n  buffer(Q1nxt, Q0cur);\n  buffer(Q2nxt, Q1cur);\n\nendmodule\n\nmodule Main(\n  input clock, preset, reset,\n  output Q2cur, Q1cur, Q0cur\n); \n\n  wire Q0nxt, Q1nxt, Q2nxt;\n  buffer(Q0nxt);\n  buffer(Q1nxt);\n  buffer(Q2nxt);\n\n  NextState nextState(\n    .Q0cur(Q0cur),\n    .Q1cur(Q1cur),\n    .Q2cur(Q2cur),\n    .Q0nxt(Q0nxt),\n    .Q1nxt(Q1nxt),\n    .Q2nxt(Q2nxt)\n  );\n\n  DFlipFlop Q0dff(\n    .d(Q0nxt),\n\t\t.c(clock),\n    .preset(preset),\n    .clear(reset),\n\t\t.Q(Q0cur)\n  );\n  \n  DFlipFlop Q1dff(\n    .d(Q1nxt),\n\t\t.c(clock),\n\t\t.Q(Q1cur),\n    .preset(reset),\n    .clear(reset)\n  );\n\n  DFlipFlop Q2dff(\n    .d(Q2nxt),\n\t\t.c(clock),\n\t\t.Q(Q2cur),\n    .preset(reset),\n    .clear(reset)\n  );\n\n  wire bar3;\n  ledbar(bar3, Q0cur, Q1cur, Q2cur);\n\n  test begin\n    #0 {preset = 0, reset = 1};\n    #1 {preset = 1, reset = 1};\n    #20;\n  end\nendmodule";