export default "// lw instruction example\n// lw is a i type instruction\n// op = 0b100011 = instr[31:26] = 35\n// rs = base address storedd in source register = instr[25:21]\n// rt = destination register = instr[20:16]\n// imm = address offset as immediate = instr[15:0]\n// eg lw $s3, 4($s4) = load 32 bit word at address $s4 + 4 into $s3\n// rf[19] = { 35, 20, 19, 4 }\n// 100011 10100 10011 0000000000000100\n\n// instruction is read from instruction memory at [pc]\n// controller(instr.op)\n//   maindec(instr.op):          => regwrite = 1, aluop = 10\n//   aludec(instr.funct, aluop): => alucontrol = 10\n// datapath(instr, alucontrol, regwrite)\n//   regfile(regwire->we, )\n\nmodule Flopr #(parameter WIDTH=32) (\n  input clk, reset,\n  input [WIDTH-1:0] d,\n  output reg [WIDTH-1:0] q\n);\n  always @ (posedge clk, posedge reset)\n    if (reset) q = 0;\n    else q = d;\nendmodule\n\nmodule Add #(parameter N = 32) (\n  input [N-1:0] a, b,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    y = a + b;\n  end\nendmodule\n\nmodule Mux2 #(parameter N=32) (\n  input [N-1:0] a, b,\n  input sel,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    case(sel)\n      // abcdefg\n      0: y = a;\n      1: y = b;\n      default: y = a;\n    endcase\n  end\nendmodule\n\nmodule Mux4 #(parameter N=32) (\n  input [N-1:0] a, b, c, d,\n  input [1:0] sel,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    case(sel)\n      // abcdefg\n      0: y = a;\n      1: y = b;\n      2: y = c;\n      3: y = d;\n      default: y = a;\n    endcase\n  end\nendmodule\n\nmodule SignExtend(\n  input [15:0] a,\n  output reg [31:0] y\n);\n  always @(*)\n    y = { { 16 { a[15] } }, a};\nendmodule\n\nmodule DataMemory(\n  input clk, we,\n  input [31:0] a, wd, // a is address in bytes = word * 4\n  output reg [31:0] rd\n);\n  reg [31:0] RAM[63:0];\n  initial\n    begin\n      RAM[3] = 999;\n      // word 3 (addressed as byte 12)\n    end\n  always @(*)\n    rd = RAM[a[31:2]]; // word aligned\n  always @ (posedge clk)\n    if (we) RAM[a[31:2]] = wd;\nendmodule\n\nmodule InstructionMemory(\n  input [5:0] a,\n  output reg [31:0] rd\n);\n  reg [31:0] RAM[63:0];\n  initial\n    begin\n      RAM[1] = 32'b100011_10100_10011_0000000000000100; \n      // lw $s3, 4($s4)\n      // lw(35) rs(20) rt(19) offset(4)\n      // lw reg19, reg20+4\n      // load into reg 19 the contents of ram at address reg20 + 4 in BYTES\n    end\n  always @(*)\n    rd = RAM[a]; // word aligned\nendmodule\n\nmodule RegisterFile (\n  input clk,\n  input we3,\n  input [4:0] ra1, ra2, wa3,\n  input [31:0] wd3,\n  output reg [31:0] rd1, rd2\n);\n  reg [31:0] rf[31:0];\n\n  initial begin\n    rf[20] = 8;\n  end\n\n  always @ (posedge clk)\n    if (we3) rf[wa3] = wd3;\n\n  always @(*)\n  begin\n    rd1 = (ra1 != 0) ? rf[ra1] : 0;\n    rd2 = (ra2 != 0) ? rf[ra2] : 0;\n  end\nendmodule\n\nmodule Alu #(parameter N=32) (\n  input [2:0] F,\n  input [N-1:0] a, b,\n  output [N-1:0] y,\n  output cout\n); \n\n  wire [N-1:0] notb;\n  assign notb = ~b;\n\n  wire [N-1:0] bb;\n  buffer(bb);\n\n  Mux2 bbmux #(N) (.a(b), .b(notb), .sel(F[2]), .y(bb));\n  \n  wire [N-1:0] aandbb, aorbb;\n  assign aandbb = a & bb;\n  assign aorbb = a | bb;\n\n  wire [N-1:0] S;\n  buffer(S);\n  buffer(cout);\n  Add adder #(N) (.a(a), .b(bb),  .y(S));\n\n  buffer(y);\n  Mux4 ymux(.a(aandbb), .b(aorbb), .c(S), .d(S[N-1]), .sel(F[1:0]), .y(y));\nendmodule\n\n// Simplified Datapath with logic for lw and sw only\nmodule Datapath (\n  input clk,\n  input reset,\n  // inputs from memory\n  input [31:0] instr,\n  input [31:0] readdata,\n  \n  // inputs from Controller\n  input regwrite,\n  input [2:0] alucontrol,\n\n  output [31:0] pc,\n  output [31:0] aluout,\n  output [31:0] writedata\n);\n\n  wire [31:0] srca, signimm, pcplus4;\n\n  // signimm = sign extended i.imm\n  SignExtend se(instr[15:0], signimm);\n\n  // next PC logic\n  Add pcadd(pc, 32'b100, pcplus4); // pcplus4 = pc + 4\n  Flopr pcreg(clk, reset, pcplus4, pc); // if clkposedge pc = pcplus4\n\n  // srca = rf[rs]\n  RegisterFile rf(\n    .clk(clk),\n    .we3(regwrite),\n    .ra1(instr[25:21]), // rs\n    .ra2(instr[20:16]), // rt\n    .wa3(instr[20:16]), // rt\n    .wd3(readdata),\n    // outputs\n    .rd1(srca),\n    .rd2(writedata)\n  );\n  \n  // aluout = srca + signimm\n  Alu alu(alucontrol, srca, signimm, aluout); \nendmodule\n\nmodule Maindec(\n  input [5:0] op,\n  output memtoreg, memwrite,\n  output branch, alusrc,\n  output regdst, regwrite,\n  output jump,\n  output [1:0] aluop\n);\n  reg [8:0] controls;\n  assign {regwrite, regdst, alusrc, branch, memwrite, memtoreg, jump, aluop} = controls;\n  always @ (*)\n    case(op)\n      6'b000000: controls = 9'b110000010; //Rtyp\n      6'b100011: controls = 9'b101001000; //LW\n      6'b101011: controls = 9'b001010000; //SW\n      6'b000100: controls = 9'b000100001; //BEQ\n      6'b001000: controls = 9'b101000000; //ADDI\n      6'b000010: controls = 9'b000000100; //J\n      default: controls = 9'bxxxxxxxxx; //???\n    endcase\nendmodule\n\nmodule Aludec (\n  input [5:0] funct,\n  input [1:0] aluop,\n  output reg [2:0] alucontrol\n);\n  always @ (*)\n    case (aluop)\n      2'b00: alucontrol = 3'b010; // add\n      2'b01: alucontrol = 3'b110; // sub\n      default: case(funct) // RTYPE\n        6'b100000: alucontrol = 3'b010; // ADD\n        6'b100010: alucontrol = 3'b110; // SUB\n        6'b100100: alucontrol = 3'b000; // AND\n        6'b100101: alucontrol = 3'b001; // OR\n        6'b101010: alucontrol = 3'b111; // SLT\n        default: alucontrol = 3'bxxx; // ???\n      endcase\n    endcase\nendmodule\n\nmodule Controller (\n  input [5:0] op, funct,\n  // input zero,\n  output memtoreg, memwrite,\n  output pcsrc, alusrc,\n  output regdst, regwrite,\n  output jump,\n  output [2:0] alucontrol)\n;\n  wire [1:0] aluop;\n  wire branch;\n  Maindec md (\n    // inputs\n    op,\n    // outputs\n    memtoreg, memwrite, branch, alusrc, regdst, regwrite, jump, aluop);\n  Aludec ad (funct, aluop, alucontrol);\n  // assign pcsrc = branch & zero;\nendmodule\n\nmodule Mips (\n  input clk, reset,\n  input [31:0] instr,\n  input [31:0] readdata,\n  output [31:0] pc,\n  output memwrite,\n  output [31:0] aluout, writedata\n);\n  wire memtoreg, branch, alusrc, regdst, regwrite, jump, zero;\n  wire [2:0] alucontrol;\n\n  Controller c(\n    // inputs\n    .op(instr[31:26]),     // 0 for r type, else lw, sw, beq \n    // .zero(zero), \n    .funct(instr[5:0]),   // add, sub, and, or, sll, srl etc\n\n    // outputs\n    .memwrite(memwrite),  // write the contents of WD into memory at address A\n    .regwrite(regwrite),  // write the contents of WD3 into register specificed by A3\n    .alucontrol(alucontrol)\n    // .pcsrc(pcsrc),        // pcnext = pcnextbr & 0 ? pcbranch : pcplus4\n    // .alusrc(alusrc),      // srcB (2nd input to ALU) = signedImm | register read data RD2\n    // .memtoreg(memtoreg),  // result (input to register write data WD3) = memory read data RD | ALUResult\n    // .regdst(regdst),      // writereg (input to register address A3) = regdst ? r.rd : i.rt\n    // .jump(jump),          // pcnext = jump ? pcjump : pcnextbr\n  );\n\n  Datapath dp(\n    // inputs\n    .clk(clk), \n    .reset(reset),\n    // memory inputs\n    .instr(instr),\n    .readdata(readdata),\n    // controller inputs\n    .regwrite(regwrite), \n    .alucontrol(alucontrol), \n    // .memtoreg(memtoreg),\n    // .pcsrc(pcsrc),\n    // .alusrc(alusrc),\n    // .regdst(regdst),\n    // .jump(jump),\n    \n    // outputs\n    .pc(pc),\n    .aluout(aluout),\n    .writedata(writedata)\n    // .zero(zero),\n  );\nendmodule\n\nmodule Main (\n  input clock,\n  input reset,\n  output [31:0] pc,\n  output [31:0] instr,\n  output [31:0] aluout,\n  output [31:0] readdata,\n  output memwrite,\n  output [31:0] writedata\n);\n\n  Mips mips(\n    .clk(clock),\n    .reset(reset),\n    .instr(instr),\n    .readdata(readdata),\n    // outputs\n    .pc(pc),\n    .memwrite(memwrite),\n    .aluout(aluout),\n    .writedata(writedata)\n  );\n\n  InstructionMemory imem(pc[7:2], instr);\n  DataMemory dmem(.clk(clock), .we(memwrite), .a(aluout), .wd(writedata), .rd(readdata));\n\n  test begin\n    #0  { reset=0 }; \n    #4;\n  end\n\n  $display begin\n    main_imem_RAM = instruction;\n    main_mips_instr = instruction;\n  end\n\nendmodule";