export default "// Comparator\n\nmodule Comparator # (parameter width = 8) (\n  input [width-1:0] a, b,\n  output reg eq, neq, lt, lte, gt, gte\n);\n  always @(*)\n  begin\n    eq  = (a == b);\n    neq = (a != b);\n    lt  = (a < b);\n    lte = (a <= b);\n    gt  = (a > b);\n    gte = (a >= b);\n  end\nendmodule\n\nmodule Main(\n  input [7:0] x, y,\n  output eq, neq, lt, lte, gt, gte\n); \n\n  Comparator #(4) compare  (\n\t\t.a(x),\n\t\t.b(y), \n\t\t.eq(eq), \n\t\t.neq(neq), \n\t\t.lt(lt), \n\t\t.lte(lte), \n\t\t.gt(gt), \n\t\t.gte(gte)\n  );\n\n  test begin\n    #0  { x=1, y=1 };\n    #1  { x=1, y=2 };\n    #2  { x=2, y=1 };\n    #3;\n  end\nendmodule";