export default "// D Flip Flop\n\nmodule DFF (\n\tinput clk, dIn,\n\toutput q3);\n\n\twire not_d_in, d_nand_a, d_nand_c, q_;\n\n\tnot( not_d_in, dIn );\n\tnand( d_nand_a, dIn, clk );\n\tnand( q3, d_nand_a, q_ );\n\tnand( d_nand_c, not_d_in, clk );\n  nand( q_, d_nand_c, q3);\nendmodule\n\nmodule DFFE (\n  input clk, dIn, dEnable,\n  output q2);\n\n  wire gatedClk;\n\n  buffer(q2);\n  and(gatedClk, clk, dEnable);\n  \n  DFF dff(.clk(gatedClk), .dIn(dIn), .q3(q2));\n\n\nendmodule\n\nmodule Main(\n  input A, E, clock,\n  output q1);\n\n  DFFE dffe(.clk(clock), .dIn(A), .dEnable(E), .q2(q1));\n\n  test begin\n    #1  { A=0, E=0 };\n    #2  { E=1 };\n    #4  { E=0 };\n    #5  { A=1 };\n    #6  { E=1 };\n    #9  { A=0, E=0 };\n    #12;\n  end\nendmodule";