export default "\n\n\nmodule Add #(parameter N = 8) (\n  input [N-1:0] a, b,\n  input cin,\n  output reg [N-1:0] y,\n  output reg cout\n);\n  always @(*)\n  begin\n    {cout, y} = a + b + cin;\n  end\nendmodule\n\nmodule flopr #(parameter WIDTH=8) (\n  input clk, reset,\n  input [WIDTH-1:0] d,\n  output reg [WIDTH-1:0] q\n);\n  always @ (posedge clk, posedge reset)\n    if (reset) q = 0;\n    else q = d;\nendmodule\n\nmodule RegisterFile (\n  input clk,\n  input we3,\n  input [4:0] ra1, ra2, wa3,\n  input [31:0] wd3,\n  output reg [31:0] rd1, rd2\n);\n  reg [31:0] rf[31:0];\n  always @ (posedge clk)\n    if (we3) rf[wa3] = wd3;\n\n  always @(*)\n  begin\n    rd1 = (ra1 != 0) ? rf[ra1] : 0;\n    rd2 = (ra2 != 0) ? rf[ra2] : 0;\n  end\nendmodule\n\n\nmodule Mux2 #(parameter N=8) (\n  input [N-1:0] a, b,\n  input sel,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    case(sel)\n      // abcdefg\n      0: y = a;\n      1: y = b;\n      default: y = a;\n    endcase\n  end\nendmodule\n\nmodule Mux4 #(parameter N=8) (\n  input [N-1:0] a, b, c, d,\n  input [1:0] sel,\n  output reg [N-1:0] y\n);\n  always @(*)\n  begin\n    case(sel)\n      // abcdefg\n      0: y = a;\n      1: y = b;\n      2: y = c;\n      3: y = d;\n      default: y = a;\n    endcase\n  end\nendmodule\n\nmodule Alu #(parameter N=8) (\n  input [2:0] F,\n  input [N-1:0] a, b,\n  output [N-1:0] y,\n  output cout\n); \n\n  wire [N-1:0] notb;\n  assign notb = ~b;\n\n  wire [N-1:0] bb;\n  buffer(bb);\n\n  Mux2 bbmux #(N) (.a(b), .b(notb), .sel(F[2]), .y(bb));\n  \n  wire [N-1:0] aandbb, aorbb;\n  assign aandbb = a & bb;\n  assign aorbb = a | bb;\n\n  wire [N-1:0] S;\n  buffer(S);\n  buffer(cout);\n  Add adder #(N) (.a(a), .b(bb), .cin(F[2]), .y(S), .cout(cout));\n\n  buffer(y);\n  Mux4 ymux(.a(aandbb), .b(aorbb), .c(S), .d(S[N-1]), .sel(F[1:0]), .y(y));\nendmodule\n\nmodule ShiftLeft2 (\n  input [31:0] a,\n  output [31:0] y\n);\n  // shift left by 2\n  always @(*)\n    y = { a[29:01], 2'b00 };\nendmodule\n\nmodule SignExtend(\n  input [15:0] a,\n  output [31:0] y\n);\n  always @(*)\n    y = { { 16 { a[15] } }, a};\nendmodule\n\nmodule FlopResettable #(parameter WIDTH = 8) (\n  input clk, reset,\n  input [WIDTH-1:0] d,\n  output reg [WIDTH-1:0] q\n);\n  always @ (posedge clk, posedge reset)\n    if (reset) q = 0; else q = d;\nendmodule\n\nmodule DataMemory(\n  input clk, we,\n  input [31:0] a, wd,\n  output [31:0] rd\n);\n  reg [31:0] RAM[63:0];\n  always @(*)\n    rd = RAM[a[31:2]]; // word aligned\n  always @ (posedge clk)\n    if (we) RAM[a[31:2]] = wd;\nendmodule\n\nmodule InstructionMemory(\n  input [5:0] a,\n  output [31:0] rd\n);\n  reg [31:0] RAM[63:0];\n  initial\n    begin\n      RAM[0] = 0x20020005; // addi $2, $0, 5\n      RAM[1] = 0x2003000c; // addi $4, $0, 12\n    end\n  always @(*)\n    rd = RAM[a]; // word aligned\nendmodule\n\nmodule Mips (\n  input clk, reset,\n  input [31:0] instr,\n  input [31:0] readdata,\n  output [31:0] pc,\n  output memwrite,\n  output [31:0] aluout, writedata\n);\n  wire memtoreg, branch, alusrc, regdst, regwrite, jump, zero;\n  wire [2:0] alucontrol;\n\n  Controller c(\n    // inputs\n    .op(instr[31:26],     // 0 for r type, else lw, sw, beq \n    .funct(instr[5:0]),   // add, sub, and, or, sll, srl etc\n    .zero(zero), \n    // outputs\n    .memwrite(memwrite),  // write the contents of WD into memory at address A\n    .regwrite(regwrite),  // write the contents of WD3 into register specificed by A3\n    .pcsrc(pcsrc),        // pcnext = pcnextbr & 0 ? pcbranch : pcplus4\n    .alusrc(alusrc),      // srcB (2nd input to ALU) = signedImm | register read data RD2\n    .memtoreg(memtoreg),  // result (input to register write data WD3) = memory read data RD | ALUResult\n    .regdst(regdst),      // writereg (input to register address A3) = regdst ? r.rd : i.rt\n    .jump(jump),          // pcnext = jump ? pcjump : pcnextbr\n    .alucontrol(alucontrol)\n  );\n\n  Datapath dp(\n    // inputs\n    .clk(clk), .reset(reset), .memtoreg(memtoreg), .pcsrc(pcsrc), .alusrc(alusrc), .regdst(regdst), .regwrite(regwrite), \n    .jump(jump), .alucontrol(alucontrol), \n    .readdata(readdata)\n    // outputs\n    .zero(zero), .pc(pc), .instr(instr), .aluout(aluout), .writedata(writeddata));\nendmodule\n\nmodule Controller (\n  input [5:0] op, funct,\n  input zero,\n  output memtoreg, memwrite,\n  output pcsrc, alusrc,\n  output regdst, regwrite,\n  output jump,\n  output [2:0] alucontrol)\n;\n  wire [1:0] aluop;\n  wire branch;\n  Maindec md (\n    // inputs\n    op,\n    // outputs\n    memtoreg, memwrite, branch, alusrc, regdst, regwrite, jump, aluop);\n  Aludec ad (funct, aluop, alucontrol);\n  assign pcsrc = branch & zero;\nendmodule\n\nmodule Maindec(\n  input [5:0] op,\n  output memtoreg, memwrite,\n  output branch, alusrc,\n  output regdst, regwrite,\n  output jump,\n  output [1:0] aluop\n);\n  reg [8:0] controls;\n  assign {regwrite, regdst, alusrc, branch, memwrite, memtoreg, jump, aluop} = controls;\n  always @ (*)\n    case(op)\n      6'b000000: controls = 9'b110000010; //Rtyp\n      6'b100011: controls = 9'b101001000; //LW\n      6'b101011: controls = 9'b001010000; //SW\n      6'b000100: controls = 9'b000100001; //BEQ\n      6'b001000: controls = 9'b101000000; //ADDI\n      6'b000010: controls = 9'b000000100; //J\n      default: controls = 9'bxxxxxxxxx; //???\n    endcase\nendmodule\n\nmodule Aludec (\n  input [5:0] funct,\n  input [1:0] aluop,\n  output reg [2:0] alucontrol\n);\n  always @ (*)\n    case (aluop)\n      2'b00: alucontrol = 3'b010; // add\n      2'b01: alucontrol = 3'b110; // sub\n      default: case(funct) // RTYPE\n        6'b100000: alucontrol = 3'b010; // ADD\n        6'b100010: alucontrol = 3'b110; // SUB\n        6'b100100: alucontrol = 3'b000; // AND\n        6'b100101: alucontrol = 3'b001; // OR\n        6'b101010: alucontrol = 3'b111; // SLT\n        default: alucontrol = 3'bxxx; // ???\n      endcase\n    endcase\nendmodule\n\nmodule Datapath (\n  input clk, reset,\n  input memtoreg, \n  input pcsrc,\n  input alusrc,\n  input regdst,\n  input regwrite, \n  input jump,\n  input [2:0] alucontrol,\n  output zero,\n  output [31:0] pc,\n  input [31:0] instr,\n  output [31:0] aluout, writedata,\n  input [31:0] readdata\n);\n  wire [4:0] writereg;\n  wire [31:0] pcnext, pcnextbr, pcplus4, pcbranch;\n  wire [31:0] signimm, signimmsh;\n  wire [31:0] srca, srcb;\n  wire [31:0] result;\n\n  // signimm = sign extended i.imm\n  signext se(instr[15:0], signimm);\n\n  // next PC logic\n  flopr #(32) pcreg(clk, reset, pcnext, pc);\n  adder pcadd1 (pc, 32'b100, pcplus4); // pcplus4 = pc + 4\n  sl2 immsh(signimm, signimmsh); // signimmsh = signimm * 4 = pc offset\n  adder pcadd2(pcplus4, signimmsh, pcbranch); // pcbranch = pcplus4 + (offset * 4)\n  mux2 #(32) pcbrmux(pcplus4, pcbranch, pcsrc,  pcnextbr); // pcnextbr = pcsrc ? pcbranch : pcplus4\n  mux2 #(32) pcmux(pcnextbr, {pcplus4[31:28],  instr[25:0], 2'b00}, jump, pcnext);\n  // pcnext = jump ? {top4bitspc j.address word_aligning_2bits} : pcnextbr\n\n  // register file logic\n  mux2 #(5) wrmux(instr[20:16], instr[15:11],  regdst, writereg); // writereg = regdst ? r.rd : r.rt\n  mux2 #(32) resmux(aluout, readdata,  memtoreg, result); // result = memtoreg ? readdata : aluout\n  regfile rf(clk, regwrite, \n    .A1(instr[25:21]), // rs\n    .A2(instr[20:16]), // rt\n    .A3(writereg),\n    .WD3(result),\n    // outputs\n    .RD1(srca),\n    .RD2(writedata));\n\n  // ALU logic\n  mux2 #(32) srcbmux(writedata, signimm, alusrc, srcb); // srcb = alusrc ? signimm : writedata\n  alu alu(srca, srcb, alucontrol, aluout, zero); // aluout = alucontrol(srca, srcb)\nendmodule\n\nmodule Main (\n  input [2:0] F,\n  input [7:0] a, b,\n  output [7:0] y,\n  output cout\n);\n\n\n\n\n  Alu alu #(8) (.F(F), .a(a), .b(b), .y(y), .cout(cout));\n\n  test begin\n    #0  { F=0, a=2, b=3 }; // AND\n    #1  { F=1, a=2, b=3 }; // OR\n    #2  { F=2, a=2, b=3 }; // +\n    #3  { F=4, a=2, b=3 }; // A AND ~B\n    #4  { F=5, a=2, b=3 }; // A OR ~B\n    #5  { F=6, a=2, b=3 }; // -\n    #6  { F=7, a=2, b=3 }; // SLT = A - B which is negative (N-1 bit is 1) if A < B\n    #7;\n  end\n\nendmodule";