export default "// Example of simplification\n// From: https://www.allaboutcircuits.com/textbook/digital/chpt-7/circuit-simplification-examples/\n\nmodule Unsimplified (\n  input a, b, c,\n  output Q );\n\n  assign Q = a & b | ((b & c) & (b | c));\nendmodule\n\nmodule Simplified (\n  input a, b, c,\n  output Q );\n\n  // a & b | ((b & c) & (b | c))\n  // &=*, |=+               AB + BC(B+C)\n  // Distribute             AB + BBC + BCC \n  // Simplify AA = A        AB + BC + BC\n  // Simplify A + A = A     AB + BC\n  // Factor                 B(A+C) \n\n  assign Q = b & (a | c);\nendmodule\n\nmodule Main(\n  input a, b, c,\n  output Qus, Qs\n); \n\n  Unsimplified unsimplified(\n\t\t.a(a),\n\t\t.b(b),\n\t\t.c(c),\n\t\t.Q(Qus)\n  );\n\n  Simplified simplified(\n\t\t.a(a),\n\t\t.b(b),\n\t\t.c(c),\n\t\t.Q(Qs)\n  );\n\n  // Check to see if Qs = Qus for all combinations of a,b,c\n  test begin\n    #0  {a=0, b=0, c=0};\n    #1  {a=0, b=0, c=1};\n    #2  {a=0, b=1, c=0};\n    #3  {a=0, b=1, c=1};\n    #4  {a=1, b=0, c=0};\n    #5  {a=1, b=0, c=1};\n    #6  {a=1, b=1, c=0};\n    #7  {a=1, b=1, c=1};\n    #8;\n  end\nendmodule";