export default " \n/*\nSum of products from truth table. \nFind minterm (contains all inputs and is true) for each true result\nSum the minterm products\n\nA B | F | minterm\n0 0 | 0 | -\n0 1 | 1 | A` * B\n1 0 | 1 | A * B`\n1 1 | 0 | -\n\nF = (A` * B) + (A * B`)\n\n*/\n\nmodule Main(\n  input A, B,\n  output F\n); \n\n  wire Fe;\n  assign Fe = (~A & B) | (A & ~B);\n\n  response(F, Fe); // buffer Fe gate to output F\n\n  test begin\n    #0  {A=0, B=0};\n    #2  {A=0, B=1};\n    #4  {A=1, B=0};\n    #6  {A=1, B=1};\n    #8;\n  end\nendmodule";