export default "// One bit full adder\n\nmodule SingleStage (\n  input a,\n  input b,\n  input cin,\n  output s,\n  output cout );\n\n  wire w1, w2, w3;\n\n  and( w1, a, b );\n  and( w2, a, cin );\n  and( w3, b, cin );\n  or( cout, w1, w2, w3 ); \n\n  xor( s, a, b, cin );\n\nendmodule\n\nmodule Main (\n  input a, b, cin,\n  output sum, cout\n);\n\n  SingleStage uut(\n\t\t.a(a),\n\t\t.b(b),\n\t\t.cin(cin),\n\t\t.s(sum),\n\t\t.cout(cout)\n  );\n\n\ttest begin\n\t\t#0  {a=0, b=0, cin=0};\n    #2  {a=0, b=0, cin=0};\n    #4  {a=0, b=0, cin=1};\n    #6  {a=0, b=1, cin=0};\n    #8  {a=0, b=1, cin=1};\n    #10 {a=1, b=0, cin=0};\n    #12 {a=1, b=0, cin=1};\n    #14 {a=1, b=1, cin=0};\n    #16 {a=1, b=1, cin=1};\n    #18;\n\tend\nendmodule";