export default "module Adder #(parameter N=8) (\n  input [N-1:0] a, b,\n  input cin,\n  output reg [N-1:0] sum,\n  output reg cout);\n  \n  always @(*)\n  begin\n    {cout, sum} = a + b + cin;\n  end\nendmodule\n\nmodule Alu (\n  input op,\n  input [7:0] A,\n  input [7:0] B,\n  output [8:0] res);\n\n  wire [7:0] sum;\n  wire co;\n\n  // reg one;\n  // initial\n  //   one <= 1'b1;\n\n  wire [7:0] b2c;\n  assign b2c = {\n      B[7] ^ op,\n      B[6] ^ op,\n      B[5] ^ op,\n      B[4] ^ op,\n      B[3] ^ op,\n      B[2] ^ op,\n      B[1] ^ op,\n      B[0] ^ op\n    };\n  \n  Adder add(.a(A), .b(b2c), .cin(op), .sum(sum), .cout(co));\n  assign res = {co, sum};\nendmodule\n\nmodule Main(\n  input op,\n  input [7:0] A,\n  input [7:0] B,\n  output [7:0] sum,\n  output co);\n\n  Alu alu(.op(op), .A(A), .B(B), .res({co,sum}));\n\n  test begin\n    #0   { op=1, A=3, B=2 }; // Q=0\n    #4   { op=0, A=3, B=2 }; // Q=0 \n    #6; // Q = 0\n  end\nendmodule";