export default "// SR Latch with Enable with data line instead of separate set and reset lines\n// the clock signals the latch to follow the data line\n// when clock is 0 the latch holds the last Q\n\nmodule DLatch (\n  input d, c,\n  output Q, Qn);\n\n  wire dn;\n  not(dn, d);\n\n  wire u1, u2, Qn;\n  assign u1 = d ~& c;\n  assign u2 = dn ~& c;\n  assign Q = u1 ~& Qn;\n  assign Qn = Q ~& u2;\nendmodule\n\nmodule DFlipFlop (\n  input d, c,\n  output Q, Qn);\n\n  wire cn, cnn;\n  not(cn, c);\n  not(cnn, cn);\n\n  wire Qmaster;\n\n  DLatch master(.d(d), .c(cn), .Q(Qmaster));\n  DLatch slave(.d(Qmaster), .c(cnn), .Q(Q), .Qn(Qn));\nendmodule\n\nmodule Main(\n  input clock, din,\n  output Out1, Out2, Out3, Out4\n); \n\n  DFlipFlop dff1(\n    .d(din),\n\t\t.c(clock),\n\t\t.Q(Out1)\n  );\n\n  DFlipFlop dff2(\n    .d(Out1),\n\t\t.c(clock),\n\t\t.Q(Out2)\n  );\n\n  DFlipFlop dff3(\n    .d(Out2),\n\t\t.c(clock),\n\t\t.Q(Out3)\n  );\n\n  DFlipFlop dff4(\n    .d(Out3),\n\t\t.c(clock),\n\t\t.Q(Out4)\n  );\n\n  test begin\n    #0 { din = 1 };\n    #2 { din = 0 };\n    #4 { din = 0 };\n    #6 { din = 0 };\n    #8;\n  end\nendmodule";