export default "/* Full adder\nSimplified instead of two half adders\n*/\n\n\nmodule FullAdder (\n  input a, b, cin,\n  output S, Cout);\n\n  wire p, g;\n\n  assign p = a ^ b;\n  assign g = a & b;\n\n  assign S = p ^ cin;\n  assign Cout = g | (p & cin); // remove brackets to test operator precedence\nendmodule\n\nmodule Main (\n  input a, b, cin,\n  output Sum, Cout\n);\n\n  FullAdder full(\n\t\t.a(a),\n\t\t.b(b),\n    .cin(cin),\n\t\t.S(Sum),\n\t\t.Cout(Cout)\n  );\n\n \n\ttest begin\n\t\t#0  {cin=0, a=0, b=0};\n    #1  {cin=0, a=0, b=1};\n    #2  {cin=0, a=1, b=0};\n    #3  {cin=0, a=1, b=1};\n\t\t#4  {cin=1, a=0, b=0};\n    #5  {cin=1, a=0, b=1};\n    #6  {cin=1, a=1, b=0};\n    #7  {cin=1, a=1, b=1};\n    #8;\n\tend\nendmodule";