export default "// SR Latch with Enable with data line instead of separate set and reset lines\n// the clock signals the latch to follow the data line\n// when clock is 0 the latch holds the last Q\n\nmodule DLatch (\n  input d, c,\n  output Q, Qn);\n\n  wire u3, u4;\n  assign u3 = d ~& c;\n  assign u4 = ~d ~& c;\n\n  assign Q = u3 ~& Qn;\n  assign Qn = u4 ~& Q;\nendmodule\n\nmodule DFlipFlop (\n  input d, c,\n  output Q, Qn);\n\n  wire cn, cnn;\n  not(cn, c);\n  not(cnn, cn);\n\n  wire Qmaster;\n\n  DLatch master(.d(d), .c(cn), .Q(Qmaster));\n  DLatch slave(.d(Qmaster), .c(cnn), .Q(Q), .Qn(Qn));\nendmodule\n\nmodule Main(\n  input clock,\n  output Count0, Count1, Count2\n); \n\n  wire Qntf1, Qntf2, Qntf3;\n\n  DFlipFlop tf1(\n    .d(Qntf1),\n\t\t.c(clock),\n\t\t.Q(Count0),\n    .Qn(Qntf1)\n  );\n\n  DFlipFlop tf2(\n    .d(Qntf2),\n\t\t.c(Qntf1),\n\t\t.Q(Count1),\n    .Qn(Qntf2)\n  );\n\n  DFlipFlop tf3(\n    .d(Qntf3),\n\t\t.c(Qntf2),\n\t\t.Q(Count2),\n    .Qn(Qntf3)\n  );\n\n  wire [2:0] dec;\n  number(dec, Count0, Count1, Count2);\n\n  test begin\n    #18;\n  end\nendmodule";