export default "// Binary Up Controller from Chapter 7.5.1 of Introduction to Logic Circuits and Logic Design LaMeres\n\n// states\n// C0 00\n// C1 01\n// C2 10\n// C3 11 then back to C0\n\n//   Current State    |       Next State   |\n//    | Q1cur | Q0Cur |    | Q1nxt | Q0nxt |\n// C0 |   0   |   0   | C1 |   0   |   1   |\n// C1 |   0   |   1   | C2 |   1   |   0   |\n// C2 |   1   |   0   | C3 |   1   |   1   |\n// C3 |   1   |   1   | C0 |   0   |   0   |\n\n// Kmap for Q1nxt\n//       \\  Q1cur\n// Q0cur  \\   0  1\n//         -------\n//       0 |  0  1 \n//       1 |  1  0\n// Q1nxt = (~Q1cur & Q0cur) | (Q1cur & ~Q0cur)\n\n// Kmap for Q0nxt\n//       \\  Q1cur\n// Q0cur  \\   0  1\n//         -------\n//       0 |  1  1 \n//       1 |  0  0\n// Q0nxt = ~Q0cur\n\nmodule DLatch (\n  input d, c,\n  output Q, Qn);\n\n  initial\n    begin\n      Q = 0;\n      Qn = 0;\n    end\n\n  wire dn;\n  not(dn, d);\n\n  wire u1, u2, Qn;\n  assign u1 = d ~& c;\n  assign u2 = dn ~& c;\n  assign Q = u1 ~& Qn;\n  assign Qn = Q ~& u2;\nendmodule\n\nmodule DFlipFlop (\n  input d, c,\n  output Q, Qn);\n\n  wire cn, cnn;\n  not(cn, c);\n  not(cnn, cn);\n\n  wire Qmaster;\n  buffer(Qmaster);\n\n  buffer(Q);\n  buffer(Qn);\n\n  DLatch master(.d(d), .c(cn), .Q(Qmaster));\n  DLatch slave(.d(Qmaster), .c(cnn), .Q(Q), .Qn(Qn));\nendmodule\n\nmodule NextState (\n  input Q1cur, Q0cur,\n  output Q1nxt, Q0nxt);\n\n  assign Q1nxt = (~Q1cur & Q0cur) | (Q1cur & ~Q0cur);\n  assign Q0nxt = ~Q0cur;\n\nendmodule\n\nmodule Main(\n  input clock,\n  output Q1cur, Q0cur\n); \n\n  wire Q1nxt, Q0nxt;\n  buffer(Q1nxt);\n  buffer(Q0nxt);\n\n  NextState nextState(\n    .Q1cur(Q1cur),\n    .Q0cur(Q0cur),\n    .Q1nxt(Q1nxt),\n    .Q0nxt(Q0nxt)\n  );\n\n  DFlipFlop Q1dff(\n    .d(Q1nxt),\n\t\t.c(clock),\n\t\t.Q(Q1cur)\n  );\n\n  DFlipFlop Q0dff(\n    .d(Q0nxt),\n\t\t.c(clock),\n\t\t.Q(Q0cur)\n  );\n\n  wire [1:0] dec;\n  number(dec, Q0cur, Q1cur);\n\n  test begin\n    #20;\n  end\nendmodule";