export default "// Master-slave d-flipflop\n\nmodule DLatch (\n  input d, c,\n  output Q, Qn);\n\n  initial\n    begin\n      Q = 0;\n      Qn = 1;\n    end\n\n  wire u3, u4;\n  assign u3 = d ~& c;\n  assign u4 = ~d ~& c;\n\n  assign Q = u3 ~& Qn;\n  assign Qn = u4 ~& Q;\nendmodule\n\nmodule DFlipFlop (\n  input d, c,\n  output Q, Qn);\n\n  wire cn, cnn;\n  not(cn, c);\n  not(cnn, cn);\n\n  wire Qmaster;\n\n  DLatch master(.d(d), .c(cn), .Q(Qmaster));\n  DLatch slave(.d(Qmaster), .c(cnn), .Q(Q), .Qn(Qn));\nendmodule\n\n\nmodule Main(\n  input d, c,\n  output Qm\n); \n\n  DFlipFlop dff(\n    .d(d),\n\t\t.c(c),\n\t\t.Q(Qm)\n  );\n\n  test begin\n    #1  { d=0, c=0 };\n    #2  { d=0, c=1 };\n    #3  { d=1, c=1 };\n    #4  { d=0, c=1 };\n    #5  { d=0, c=0 };\n    #6  { d=1, c=1 };\n    #7  { d=0, c=1 };\n    #8  { d=1, c=1 };\n    #9  { d=1, c=0 };\n    #10 { d=1, c=0 };\n    #11 { d=1, c=0 };\n    #12;\n  end\nendmodule";