export default "// SR Latch\n// s=1, r=0 = set Q to 1\n// s=0, r=1 = reset Q to 0\n// s=0, r=0 = hold/store current value of Q\n// s=1, r=1 = not allowed\n\nmodule SRLatch (\n  input r, s,\n  output Q );\n\n  wire Qn;\n\n  initial\n    Q=0;\n    \n  assign Q = r ~| Qn;\n  assign Qn = Q ~| s;\n  \nendmodule\n\nmodule Main(\n  input r, s,\n  output Q\n); \n\n  SRLatch latch(\n\t\t.r(r),\n\t\t.s(s),\n\t\t.Q(Q)\n  );\n\n  test begin\n    #0  {s=0, r=1};\n    #1  {s=1, r=0};\n    #2  {s=0, r=0};\n    #5  {s=0, r=1};\n    #8  {s=1, r=0};\n    #10 {s=0, r=0};\n    #12;\n  end\nendmodule";