export default "module Register #(parameter WIDTH = 8) (\n  input clk,\n  input [WIDTH-1:0] D,\n  input EI,\n  output reg [WIDTH-1:0] Q)\n;\n  wire CLK;\n  assign CLK=(clk & EI);\n  always @(posedge CLK)\n    begin\n      Q <= D;\n    end\nendmodule\n\nmodule Main(\n  input clock, D, EI,\n  output Q);\n  Register regA(.clk(clock), .D(D), .Q(Q), .EI(EI));\n  test begin\n    #0  { D=0, EI=1 }; // Q=0\n    #4  { D=1, EI=1 }; // Q=0 \n    #6  { D=0, EI=0 }; // Q=1  \n    #8  { D=1, EI=0 }; // Q=1   \n    #10  { D=1, EI=1 }; // Q=1   \n    #12  { D=0, EI=1 }; // Q=1   \n    #14; // Q = 0\n  end\nendmodule";