export default "// SR Latch with Enable with data line instead of separate set and reset lines\n// the clock signals the latch to follow the data line\n// when clock is 0 the latch holds the last Q\n\nmodule DLatch (\n  input d, c,\n  output Q, Qn);\n\n  initial Q = 0;\n  initial Qn = 1;\n\n  wire u3, u4;\n  assign u3 = d ~& c;\n  assign u4 = ~d ~& c;\n\n  assign Q = u3 ~& Qn;\n  assign Qn = u4 ~& Q;\nendmodule\n\nmodule Main(\n  input d, c,\n  output Q, Qn\n); \n\n  DLatch latch(\n    .d(d),\n\t\t.c(c),\n\t\t.Q(Q)\n  );\n\n  test begin\n    #0  {c=0, d=0};\n    #1  {c=0, d=1};\n    #2  {c=0, d=0};\n    #3  {c=0, d=1};\n    #4  {c=1, d=0};\n    #5  {c=1, d=1};\n    #6  {c=1, d=0};\n    #7  {c=1, d=1};\n    #8  {c=0, d=0};\n    #9  {c=0, d=1};\n    #10  {c=0, d=0};\n    #11  {c=0, d=1};\n    #12;\n  end\nendmodule";