export default "// 4 bit adder behavioural model (experimental)\n\nmodule FullAdder (\n  input a, b, ci,\n  output sum, cout );\n\n  reg [1:0] res;\n  always @(*)\n    begin\n      res = a + b + ci;\n    end\n\n  assign sum = res[0];\n  assign cout = res[1];\nendmodule\n\nmodule Add4 (\n  input [3:0] a, b,\n  input ci,\n  output [3:0] sum,\n  output cout );\n\n  wire [2:0] cob;\n\n  FullAdder fa0(.a(a[0]), .b(b[0]), .ci(ci),     .sum(sum[0]), .cout(cob[0]));\n  FullAdder fa1(.a(a[1]), .b(b[1]), .ci(cob[0]), .sum(sum[1]), .cout(cob[1]));\n  FullAdder fa2(.a(a[2]), .b(b[2]), .ci(cob[1]), .sum(sum[2]), .cout(cob[2]));\n  FullAdder fa3(.a(a[3]), .b(b[3]), .ci(cob[2]), .sum(sum[3]), .cout(cout));\nendmodule\n\n\nmodule Main(\n  input [3:0] x, y,\n  input ci,\n  output [3:0] SUM, COUT\n); \n\n  Add4 myAdder(\n\t\t.a(x),\n\t\t.b(y),\n    .ci(ci),\n\t\t.sum(SUM),\n\t\t.cout(COUT)\n  );\n\n  test begin\n    #0  { ci=0, x=0, y=0 };\n    #1  { ci=0, x=0, y=1 };\n    #2  { ci=0, x=1, y=1 };\n    #3  { ci=0, x=1, y=2 };\n    #4  { ci=0, x=1, y=2 };\n    #5  { ci=0, x=1, y=2 };\n    #6  { ci=0, x=2, y=2 };\n    #8;\n  end\nendmodule";