export default "// One bit half adder\n\nmodule HalfAdder (\n  input a, b,\n  output S, Cout);\n\n  assign S = a ^ b;\n  assign Cout = a & b;\nendmodule\n\nmodule Main (\n  input a, b,\n  output Sum, Cout\n);\n\n  HalfAdder half(\n\t\t.a(a),\n\t\t.b(b),\n\t\t.S(Sum),\n\t\t.Cout(Cout)\n  );\n\n\ttest begin\n\t\t#0  {a=0, b=0};\n    #2  {a=0, b=1};\n    #4  {a=1, b=0};\n    #6  {a=1, b=1};\n    #8;\n\tend\nendmodule";