export default "// SR Latch with Enable\n\nmodule SRLatchComplement (\n  input s, r,\n  output Q );\n\n  wire Qn;\n  assign Q = s ~& Qn;\n  assign Qn = Q ~& r;\nendmodule\n\nmodule SRLatchEnable (\n  input r, s, c,\n  output Q );\n\n  wire u1, u2, Qn;\n\n  assign u1 = s ~& c;\n  assign u2 = r ~& c;\n\n  SRLatchComplement latchC(.s(u1), .r(u2), .Q(Q));\n endmodule\n\nmodule Main(\n  input r, s, c,\n  output Q\n); \n\n  SRLatchEnable latchE(\n    .r(r),\n\t\t.s(s),\n\t\t.c(c),\n\t\t.Q(Q)\n  );\n\n  test begin\n    #0  {c=1, s=0, r=0};\n    #2  {c=1, s=1, r=0};\n    #4  {c=1, s=0, r=0};\n    #8  {c=1, s=0, r=1};\n    #10 {c=1, s=0, r=0};\n    #12 {c=0, s=1, r=0}; // no effect\n    #14;\n  end\nendmodule";