export default "// S`R` Latch\n// complement of SR Latch using nand gates\n// function of s and r are reversed\n// s=1, r=0 = reset Q to 0\n// s=0, r=1 = set Q to 1\n// s=1, r=1 = hold/store current value of Q\n// s=0, r=0 = not allowed\n\nmodule SRLatchComplement (\n  input r, s,\n  output Q );\n\n  wire Qn;\n  assign Q = s ~& Qn;\n  assign Qn = Q ~& r;\nendmodule\n\nmodule Main(\n  input r, s,\n  output Q\n); \n\n  SRLatchComplement latch(\n    .r(r),\n\t\t.s(s),\n\t\t.Q(Q)\n  );\n\n  test begin\n    #0  {r=1, s=1};\n    #2  {s=1, r=0};\n    // #4  {s=1, r=1}; do not do this\n    #4  {s=1, r=1};\n    #8  {s=0, r=1};\n    #10 {s=1, r=1};\n    #12;\n  end\nendmodule";